<template>

  <el-container id="container">
    <!--头部-->
    <el-header id="header">
      <el-row>
        <el-col :span="2" :offset="1"><img id="logo" src="../assets/images/logo.png"/></el-col>
        <el-col :span="10">
          <el-menu :default-active="defaultActive" class="el-menu-demo" mode="horizontal"><!--@select="handleSelect"-->
            <el-menu-item index="1">常用工具</el-menu-item>
            <el-menu-item index="2" disabled>编程工具</el-menu-item>
          </el-menu>
        </el-col>
        <el-col :span="3" :offset="5">
          <el-menu :default-active="1" class="el-menu-demo" mode="horizontal"><!--@select="handleSelect"-->
            <el-menu-item index="1" disabled>反馈</el-menu-item>
            <el-menu-item index="2"><a href="https://www.ele.me" target="_blank">GitHub</a></el-menu-item>
          </el-menu>
        </el-col>
      </el-row>
    </el-header>
    <!--主体-->
    <el-main id="main">
      <el-row class="tool">
        <el-col :span="18" :offset="3">
          <el-card class="box-card" shadow="hover">
            <div slot="header" class="clearfix">
              <el-button @click="dialogVisible = true" round class="title">名片生成器</el-button>
            </div>
            <p>通过人物的基本信息进行二维码的生成，扫描生成的二维码即可直接添加联系人。</p>
            <p>基本信息包括：名称/昵称/手机号/地址/邮箱/公司/个人主页...</p>
          </el-card>
          <el-dialog
            title="名片生成器"
            :visible.sync="dialogVisible"
            width="50%"
            :before-close="handleClose">
            <el-form ref="form" :model="form" label-width="auto">
              <!--输入信息-->
              <div v-if="!form.hasGen">
                <el-row>
                  <el-col :span="11" :offset="1">
                    <el-form-item label="姓名">
                      <el-input v-model="form.name"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="11">
                    <el-form-item label="昵称">
                      <el-input v-model="form.nickname"></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="11" :offset="1">
                    <el-form-item label="手机号">
                      <el-input v-model="form.phone"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="11">
                    <el-form-item label="电子邮箱">
                      <el-input v-model="form.email"></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="11" :offset="1">
                    <el-form-item label="地址">
                      <el-input v-model="form.address"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="11">
                    <el-form-item label="生日">
                      <el-date-picker type="date" placeholder="选择日期" v-model="form.birth" format="yyyyMMdd" value-format="yyyyMMdd" style="width: 100%;"/>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="11" :offset="1">
                    <el-form-item label="公司">
                      <el-input v-model="form.company"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="11">
                    <el-form-item label="职位/角色">
                      <el-input v-model="form.role"></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="11" :offset="1">
                    <el-form-item label="网站/个人主页">
                      <el-input v-model="form.homepage"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="11">
                    <el-form-item label="备注">
                      <el-input v-model="form.memo"></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
              </div>
              <!--生成的二维码-->
              <div v-show="form.hasGen">
                <el-row>
                  <el-col :span="12" :offset="6">
                    <qrcode :value="myCode" :options="{ width: 200 }"></qrcode>
                  </el-col>
                </el-row>
              </div>
              <!--立即生成按钮-->
              <el-row>
                <el-col v-if="!form.hasGen" :span="4" :offset="8">
                  <el-form-item>
                    <el-button type="primary" @click="generateCode">立即生成</el-button>
                  </el-form-item>
                </el-col>
              </el-row>
              <!--重新生成按钮-->
              <el-row>
                <el-col v-if="form.hasGen" :span="8" :offset="8">
                  <el-form-item>
                    <br>
                    <el-button @click="form.hasGen = false">重新生成</el-button>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
          </el-dialog>
        </el-col>
      </el-row>

    </el-main>
    <!--尾部-->
    <el-footer id="footer">
      <p>djzhao</p>
    </el-footer>
  </el-container>
</template>

<script>
  export default {
    name: "home",
    data() {
      return {
        defaultActive: '1',
        dialogVisible: false,
        form: {
          name: '',
          nickname: '',
          phone: '',
          email: '',
          company: '',
          role: '',
          homepage: '',
          address: '',
          birth: '',
          memo: '',
          hasGen: false
        },
        myCode: 'Hi, Nice to Meet U!'
      };
    },
    methods: {
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {
          });
      },
      generateCode() {
        this.form.hasGen = true;
        //  MECARD:N:John Doe;SOUND:read;TEL:555-555-5555;EMAIL:email@example.coma;NOTE:Contosoa;BDAY:20190925;ADR:南京市江宁区弘景大道3601号 南京晓庄学院;URL:http://www.example.com;NICKNAME:dj;
        let codeStr = `MECARD:N:${this.form.name};TEL:${this.form.phone};EMAIL:${this.form.email};NOTE:${this.form.memo};BDAY:${this.form.birth};ADR:${this.form.address};URL:${this.form.homepage};ROLE:${this.form.role};ORG:${this.form.company};NICKNAME:${this.form.nickname};`;
        this.myCode = codeStr;
      }
    }
  }
</script>

<style scoped>
  #container {
    height: 100vh;
    background: #fff;
  }

  #header {
    box-shadow: 0 3px 3px #DCDFE6;
  }

  #logo {
    width: 40px;
    margin: 10px;
  }

  #main {
    height: 100%;
    overflow-y: auto;
  }

  #main .tool {
    /*cursor: pointer;*/
  }

  #main .tool .title {
    font-size: 1rem;
    color: #409EFF;
  }

  #main .tool p {
    font-size: 1rem;
    line-height: 1.5rem;
    color: #909399;
  }

  #footer {
    background: #409EFFcc;
  }


</style>



